.header-image img {
    width: 150px;
    height: auto;
}

.search-bar {
    text-align: center;
    margin: 20px;
}

.search-bar form input {
    width: 60%;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.search-bar form button {
    padding: 10px 20px;
    background-color: #9651ED;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.search-bar form button:hover {
    background-color: rebeccapurple;
}

.results {
    margin: 20px;
    padding: 10px;
    background: #f4f4f4;
}
